// breakpoint
$MQs: true;

@mixin breakpoint($point) {
  	@if ($MQs) {
	    $bp-mobile: "(max-width: 40em)";
	    $bp-mobile-wide: "(max-device-width: 736px) and (orientation: landscape)";
	    $bp-tablet: "(max-width: 70em)";
	    $bp-tablet-wide: "(min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape) and (-webkit-min-device-pixel-ratio: 1)";
	    $bp-desktop: "(max-width: 100em)";

	    @if $point == desktop {
	      	@media #{$bp-desktop} {
	      		@content; } }

	    @else if $point == tablet {
	      	@media #{$bp-tablet} {
	      		@content; } }

	    @else if $point == tablet-wide {
	      	@media #{$bp-tablet-wide} {
	      		@content; } }

	    @else if $point == mobile {
	      	@media #{$bp-mobile} {
	      		@content; } }

	    @else if $point == mobile-wide {
	      	@media #{$bp-mobile-wide} {
	      		@content; } } } }



// clearfix
%clearfix {
	*zoom: 1;
	&:before, &:after {
		content: " ";
		display: table; }
	&:after {
		clear: both; } }



// pseudo
@mixin pseudo($display: block, $pos: absolute, $content: '') {
    content: $content;
    display: $display;
    position: $pos; }



// Responsive ratio
@mixin responsive-ratio($x,$y, $pseudo: false) {
    $padding: unquote( ( $y / $x ) * 100 + '%' );
    @if $pseudo {
        &:before {
            @include pseudo($pos: relative);
            width: 100%;
            padding-top: $padding; } }
    @else {
        padding-top: $padding; } }



// ratina
@mixin ratina($image, $width, $height) {
	@media (min--moz-device-pixel-ratio: 1.3),(-o-min-device-pixel-ratio: 2.6/2),(-webkit-min-device-pixel-ratio: 1.3),(min-device-pixel-ratio: 1.3),(min-resolution: 1.3dppx) {
	    background-image: url($image);
	    background-size: $width $height; } }



// fontsize
@mixin font-size($size) {
	font-size: $size * 1px;
	font-size: $size * 0.1rem; }



// aniamation
@mixin keyframes($animation-name) {
    @keyframes #{$animation-name} {
        @content; } }
@mixin animation($str) {
	animation: #{$str}; }



// gradient
@mixin background-gradient($start-color, $end-color, $orientation) {
    background: $start-color;
    @if $orientation == 'vertical' {
    	background: linear-gradient(to bottom, $start-color, $end-color); }
    @else if $orientation == 'horizontal' {
    	background: linear-gradient(to right, $start-color, $end-color); }
    @else {
    	background: radial-gradient(ellipse at center, $start-color, $end-color); } }






