// variable

//////////////////////////////////////

$main: #ca2027;
$white: #ffffff;
$gray: #dadada;
$gray-d: #b8b8b8;
$gray-dd: #6f6f6f;
$gray-l: #e6e6e6;
$gray-ll: #f0f0f0;

$transition: 0.5s;








//////////////////////////////////////

// global

//////////////////////////////////////

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'db_adman', serif;
	font-weight: normal; }

html {
	font-size: 62.5%; }
body {
	background: $white; }
a {
	text-decoration: none;
	display: block;
	cursor: pointer;
	font-family: 'db_adman', serif; }


textarea {
	font-family: 'db_adman', serif; }
.container {
	width: 80%;
	display: block;
	margin: 0 auto;
	position: relative; }

img {
	max-width: 100%; }
ul {
	list-style: none; }

p,a {
	@include font-size(15);
	&:hover {
		color: $main;
		transition: $transition; } }

.fa {
	@include font-size(25);
	color: $main;
	background: transparent;
	padding: .22em .3em {
    border-radius: 50%; } }


h1 {
	@include font-size(48); }
h2 {
	@include font-size(40); }
h3 {
	@include font-size(31); }
h4 {
	@include font-size(25); }
h5 {
	@include font-size(20); }
h6 {
	@include font-size(16); }
p {
	@include font-size(15); }
input {
	@include font-size(15);
	background: transparent;
	border: 0;
	width: 100%;
	font-family: 'db_adman', serif;
	&:focus,
	&:active {
		outline: 0; } }


select {
	font-family: 'db_adman', serif;
	background: transparent; }


button {
	font-family: 'db_adman', serif; }











